import React from 'react';
import PropTypes from 'prop-types';
import { Grid } from '@material-ui/core';

import { ContentBox } from '../../../components/ContentBox';

const TwoColumnLayout = ({children}) => {
    return (
        <Grid container spacing={0}>
            <Grid item sm={12} md={3} lg={3} className="leftColumn">
               {
                   children.filter(child => child.type.name === 'LeftPane')
               }
                
            </Grid>
            <Grid item sm={12} md={9} lg={9} className="RightColumn">
                {
                   children.filter(child => child.type.name === 'RightPane')
               }
            </Grid>
        </Grid>
    )
}

TwoColumnLayout.propTypes = {
    children: PropTypes.element,
};

TwoColumnLayout.defaultProps = {
    children: null,
}

export default TwoColumnLayout;